import React, {Component} from 'react';
import {ProductBox, SectionHeader, ColumnHeader} from '../components/Sections/Sections';
import {SectionCarousel} from '../components/Carousel/Carousel';
import {ProductCard, SecondProductCard} from '../components/Cards/Cards';
import {collectionsData, productsData} from '../global/variables';

class Shop extends Component {
    constructor(props) {
        super(props);
        this.state = {
            products: productsData,
            collections: collectionsData
        }
    }

    render(){
        if(!this.state.products) return null;

        let products = this.state.products.reverse();
        let collections = this.state.collections;

        let randomProductNum0 = Math.floor(Math.random() * ((products.length - 1) - 0 + 1)) + 0;
        let randomProductNum1 = Math.floor(Math.random() * ((products.length - 1) - 0 + 1)) + 0;
        let randomProductNum2 = Math.floor(Math.random() * ((products.length - 1) - 0 + 1)) + 0;

        return(
            <div className="view">
                <SectionCarousel title="CỬA HÀNG"/>
                <section>
                    <SectionHeader text="MÙA XUÂN NÀY CÓ GÌ?" title="Các Mặt Hàng Nổi Bật"/>
                    <div className="row">
                        <ProductBox title="Collection 1" text="Spring 2021" action="SHOP NOW" action_link="/shop/0"/>
                        <ProductBox title="Collection 2" text="Spring 2021" action="SHOP NOW" action_link="/shop/1"/>
                        <ProductBox title="Collection 3" text="Spring 2021" action="SHOP NOW" action_link="/shop/2"/>
                    </div>
                    <section>
                        <SectionHeader text="CÓ GÌ MỚI?" title="Sản Phẩm Mới" action="Xem Tất Cả" action_link="/shop/all"/>
                        <div className="list">
                            {products.map((data, i) => {
                                if(i < 6) {
                                    return <ProductCard title={data.title} price={data.price} image={data.banner} product_id={data._id} key={`new-product-${i}`}/>
                                }
                            })}
                        </div>
                    </section>
                    {collections.map((item, itemI) => {
                        return(
                            <section>
                                <SectionHeader title={item.title} action="Xem Tất Cả" action_link={`/shop/${item._id}`}/>
                                <div className="list">
                                    {products.map((data, i) => {
                                        if(data.type == item._id) {
                                            return <ProductCard title={data.title} price={data.price} image={data.banner} product_id={data._id} key={`product-0-${i}`}/>
                                        }
                                    })}
                                </div>
                            </section>
                        )
                    })}
                    <section>
                        <div className="list">
                            <div className="column_l">
                                <ColumnHeader title="Hoa nổi bật"/>
                                {products.map((data, i) => {
                                    if(i >= randomProductNum0 && i < randomProductNum0 + 4) {
                                        return <SecondProductCard title={data.title} price={data.price} image={data.banner} product_id={data._id} key={`product-1-${i}`}/>
                                    }
                                })}
                            </div>
                            <div className="column_l">
                                <ColumnHeader title="Hoa bán chạy"/>
                                {products.map((data, i) => {
                                    if(i >= randomProductNum1 && i < randomProductNum1 + 4) {
                                        return <SecondProductCard title={data.title} price={data.price} image={data.banner} product_id={data._id} key={`product-2-${i}`}/>
                                    }
                                })}
                            </div>
                            <div className="column_l">
                                <ColumnHeader title="Hoa mới nhất"/>
                                {products.map((data, i) => {
                                    if(i >= randomProductNum2 && i < randomProductNum2 + 4) {
                                        return <SecondProductCard title={data.title} price={data.price} image={data.banner} product_id={data._id} key={`product-3-${i}`}/>
                                    }
                                })}
                            </div>
                        </div>
                    </section>
                </section>
                
            </div>
        )
    }
}

export default Shop;