import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import {IoMenuSharp,IoSearchOutline, IoCloseOutline} from 'react-icons/io5';
import {AiOutlineMenu, AiOutlineSearch} from 'react-icons/ai';
import {collectionsData} from '../../global/variables';

import Logo from '../../styles/assets/img/HoaTuoiTuongYLogo.svg';

import "./styles.scss";

export const HeaderNavigationBar = (props) => {
    return (
        <div className="nav child_section">
            <div className="container">
                <div className="left">
                    <span>TƯỜNG Ý FLOWER SHOP</span>
                </div>
                <div className="right">
                    <span>HOTLINE: +84 0979.789.840</span>
                </div>
            </div>
        </div>
    )
}

export class NavigationBar extends Component {
    constructor(props) {
        super(props);

        this.state = {
            search: false,
            onDrawer: false
        }
    }

    render() {
        
        if(this.state.onDrawer) {
            return <MenuDrawer onClose={() => this.setState({onDrawer: false})}/>
        }

        if(this.state.search) {
            return(
                <nav>
                    <div className="section" style={{height: 70}}>
                        <div className="search_container">
                            <AiOutlineSearch className="icon"/>
                            <input type="text" placeholder="Tìm kiếm..."/>
                            <IoCloseOutline className="icon close" onClick={() => this.setState({search: false})}/>
                        </div>
                    </div>
                </nav>
            )
        }
        
        return(
            <nav>
                <div className="section" style={{height: 70}}>
                    <div className="menu_btn" onClick={() => this.setState({onDrawer: true})}><AiOutlineMenu className="icon"/></div>
                    <Link to="/" className="logo"><img src={Logo}/></Link>
                    <div className="icon_container">
                        <AiOutlineSearch className="icon" onClick={() => this.setState({search: true})}/>
                    </div>
                    <ul className="nav">
                        <li><Link to="/">TRANG CHỦ</Link></li>
                        <li><Link to="/shop">CỬA HÀNG</Link>
                            <div className="sub">
                                <ul className="section">
                                    {collectionsData.map((data, i) => {
                                        return(
                                            <li key={`item-${i}`}>
                                                <Link to={`/shop/${data._id}`}>
                                                    <div className="image" style={{background: `url(/images/${data.banner}) no-repeat center  center / cover`}}></div>
                                                    <span>{data.title}</span>
                                                </Link>
                                            </li>
                                        )
                                    })}
                                </ul>
                            </div>
                        </li>
                        <li><Link to="/gallery">GALLERY</Link></li>
                        <li><Link to="/news">TIN TỨC</Link></li>
                        <li><Link to="/contact">LIÊN HỆ</Link></li>
                    </ul>
                </div>
            </nav>
        )
    }
}

const MenuDrawer = (props) => { 
    return(
        <div className="menu_drawer">
            <IoCloseOutline className="closeBtn" onClick={props.onClose}/>
            <ul>
                <li onClick={props.onClose}><Link to="/">TRANG CHỦ</Link></li>
                <li onClick={props.onClose}><Link to="/shop">CỬA HÀNG</Link></li>
                <li onClick={props.onClose}><Link to="/gallery">GALLERY</Link></li>
                <li onClick={props.onClose}><Link to="/news">TIN TỨC</Link></li>
                <li onClick={props.onClose}><Link to="/contact">LIÊN HỆ</Link></li>
            </ul>
        </div>
    )
}