import React, {Component} from 'react';
import {Breadcrumb} from '../components/Breadcrumb/Breadcrumb';
import {ProductSummary, ProductTabs} from '../components/Product/Product';
import {SectionHeader, ColumnHeader} from '../components/Sections/Sections';
import {ProductCard, SecondProductCard} from '../components/Cards/Cards';
import {collectionsData, productsData, findWithAttr} from '../global/variables';

class ProductView extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: productsData[findWithAttr(productsData, '_id', this.props.match.params.id)],
            products: productsData
        }    
    }

    render() {
        if(!this.state.data) return null;

        let data = this.state.data;
        let products = this.state.products;

        let randomProductNum0 = Math.floor(Math.random() * ((products.length - 1) - 0 + 1)) + 0;
        let randomProductNum1 = Math.floor(Math.random() * ((products.length - 1) - 0 + 1)) + 0;
        let randomProductNum2 = Math.floor(Math.random() * ((products.length - 1) - 0 + 1)) + 0;
        let randomProductNum3 = Math.floor(Math.random() * ((products.length - 1) - 0 + 1)) + 0;

        return(
            <div className="view">
                <section>
                    <Breadcrumb path={`shop/${data.title}`}/>
                </section>
                <section>   
                    <ProductSummary data={data}/>
                </section>
                <section>
                    <ProductTabs data={data}/>
                </section>
                <section>
                    <SectionHeader title="Những mẫu hoa cùng loại" action="Xem Tất Cả" action_link="/shop/all"/>
                    <div className="list">
                        {products.map((data, i) => {
                            if(i >= randomProductNum0 && i < randomProductNum0 + 3) {
                                return <ProductCard title={data.title} price={data.price} image={data.banner} product_id={data._id} key={`product-0-${i}`}/>
                            }
                        })}
                    </div>
                </section>
                <section>
                    <div className="list">
                        <div className="column_l">
                            <ColumnHeader title="Hoa nổi bật"/>
                            {products.map((data, i) => {
                                if(i >= randomProductNum1 && i < randomProductNum1 + 4) {
                                    return <SecondProductCard title={data.title} price={data.price} image={data.banner} product_id={data._id} key={`product-1-${i}`}/>
                                }
                            })}
                        </div>
                        <div className="column_l">
                            <ColumnHeader title="Hoa bán chạy"/>
                            {products.map((data, i) => {
                                if(i >= randomProductNum2 && i < randomProductNum2 + 4) {
                                    return <SecondProductCard title={data.title} price={data.price} image={data.banner} product_id={data._id} key={`product-2-${i}`}/>
                                }
                            })}
                        </div>
                        <div className="column_l">
                            <ColumnHeader title="Hoa mới nhất"/>
                            {products.map((data, i) => {
                                if(i >= randomProductNum3 && i < randomProductNum3 + 4) {
                                    return <SecondProductCard title={data.title} price={data.price} image={data.banner} product_id={data._id} key={`product-3-${i}`}/>
                                }
                            })}
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}

export default ProductView;