import React, {Component} from 'react';
import {RiArrowDropRightLine} from 'react-icons/ri';
import {AiOutlineHome} from 'react-icons/ai';
import {Link} from 'react-router-dom';

import "./styles.scss";

class Breadcrumb extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        let path = this.props.path.split("/");

        return(
              <ol className="breadcrumb">
                <li className="breadcrumb-item"><Link to="/"><AiOutlineHome className="icon home"/></Link></li>
                {path.map((data) => {
                    return (
                        <>
                            <li className="breadcrumb-item"><RiArrowDropRightLine className="icon"/></li>
                            <li className="breadcrumb-item"><Link href="#">{data}</Link></li>
                        </>
                    )
                })}
              </ol>
        )
    }
}

export {
    Breadcrumb
}