import React, {Component} from 'react';
import {Breadcrumb} from '../components/Breadcrumb/Breadcrumb';
import {SectionControl} from '../components/Sections/Sections';
import {ProductCard, SecondProductCard} from '../components/Cards/Cards';
import {SectionCarousel} from '../components/Carousel/Carousel';
import {collectionsData, productsData} from '../global/variables';

class ShopChild extends Component {
    constructor(props) {
        super(props);

        this.state = {
            data: productsData,
            collections: collectionsData
        }
    }

    render() {
        if(!this.state.data) return null;

        return(
            <div className="view">
                <SectionCarousel title={`${this.props.match.params.id !== 'all' ? this.state.collections[this.props.match.params.id].title.toUpperCase() : 'CÁC SẢN PHẨM MỚI'}`}/>
                <section>
                    <Breadcrumb path={`shop/${this.props.match.params.id !== 'all' ? this.state.collections[this.props.match.params.id].title : 'All'}`}/>
                </section>
                <section>
                    <SectionControl/>
                    <div className="list">
                        {this.state.data.reverse().map((data, i) => {
                            if(this.props.match.params.id == 'all') {
                                return <ProductCard title={data.title} price={data.price} image={data.banner} product_id={data._id} key={`product-${i}`}/>
                            }else if(data.type == this.props.match.params.id) {
                                return <ProductCard title={data.title} price={data.price} image={data.banner} product_id={data._id} key={`product-${i}`}/>
                            }
                        })}
                    </div>
                </section>
            </div>
        )
    }
}

export default ShopChild;