import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom';

//COMPONENTS
import {HeaderNavigationBar, NavigationBar} from './components/NavigationBar/NavigationBar';
import Footer from './components/Footer/Footer';
import ScrollToTop from './components/ScrollToTop/ScrollToTop';

//VIEWS
import Home from './views/Home';
import ProductView from './views/ProductView';
import News from './views/News';
import NewsReading from './views/NewsReading';
import Shop from './views/Shop';
import ShopChild from './views/ShopChild';
import Gallery from './views/Gallery';
import Contact from './views/Contact';

class App extends Component {
  constructor(props) {
    super(props);
  }

  message = "HELLO";
  render() {
    return (
      <div>
        <HeaderNavigationBar/>
        <NavigationBar/>
        <Main />
        <Footer/>
      </div>
    );
  }
}

class Main extends Component {
  render() {
      return (
        <main>
        <ScrollToTop>
            <Switch>
              <Route path='/gallery' component={Gallery}/>
              <Route path='/product/:id' component={ProductView}/>
              <Route path='/shop/:id' component={ShopChild}/>
              <Route path='/shop' component={Shop}/>
              <Route path='/news/:id' component={NewsReading}/>
              <Route path='/news' component={News}/>
              <Route path='/contact' component={Contact}/>
              <Route path='/' component={Home}/>
            </Switch>
          </ScrollToTop>
        </main>
      )
  }
}

export default App;