import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import {BsArrowRight} from 'react-icons/bs';
import {IoIosArrowForward} from 'react-icons/io';

import "./styles.scss";

class ProductCard extends Component{
    render() {
        return(
            <Link to={`/product/${this.props.product_id}`} className="card product">
                <div className="image_container">
                    <Link to={`/product/${this.props.product_id}`}>Xem sản phẩm</Link>
                    <div className="image" style={{background: `url(/images/${this.props.image}) no-repeat center  center / cover`}}></div>
                </div>
                <div className="information_container">
                        <div className="info">
                            <Link to={`/product/${this.props.product_id}`}>{this.props.title}</Link>
                            <span>{this.props.price > -1 ? this.props.price + 'đ' : 'LIÊN HỆ'}</span>
                        </div>
                        <div className="control">
                            <BsArrowRight className="icon"/>
                        </div>
                </div>
            </Link>
        )
    }
}

