import React, {Component} from 'react';
import {CarouselFullPage} from '../components/Carousel/Carousel'
import {ProductBox, SectionHeader} from '../components/Sections/Sections';
import {ProductCard, NewsCard, SecondNewsCard} from '../components/Cards/Cards';
import {productsData} from '../global/variables';

class Home extends Component {
    render() {
        return(
            <div>
                <CarouselFullPage/>
                <section>
                    <SectionHeader text="MÙA XUÂN NÀY CÓ GÌ?" title="Các Mặt Hàng Nổi Bật"/>
                    <div className="row">
                        <ProductBox title="Collection 1" text="Spring 2021" action="SHOP NOW" action_link="/shop/0"/>
                        <ProductBox title="Collection 2" text="Spring 2021" action="SHOP NOW" action_link="/shop/1"/>
                        <ProductBox title="Collection 3" text="Spring 2021" action="SHOP NOW" action_link="/shop/2"/>
                    </div>
                </section>
                <section>
                    <SectionHeader text="CÓ GÌ MỚI?" title="Sản Phẩm Mới" action="Xem Tất Cả" action_link="/shop/all"/>
                    <div className="list">
                        {productsData.reverse().map((item, i) => {
                            if(i < 6) {
                                return <ProductCard title={item.title} price={item.price} image={`${item.banner}`} product_id={item._id}  key={`product-new-${i}`}/>
                            }
                        })}
                    </div>
                </section>
                <section>
                    <SectionHeader text="HÃY XEM TIN TỨC!" title="Tin Tức Mới Nhất" action="Xem Tất Cả" action_link="/news"/>
                    <NewsCard title="Hoa mới của Laven" text="December 12, 2020" image="https://www.apple.com/newsroom/images/passions/education/Apple_gallaudet-university_10072020.jpg.landing-gradient-large.jpg"/>

                    <div className="row">
                        <div className="list">
                            <SecondNewsCard title="Hoa mới của Laven" text="December 12, 2020" image="https://www.apple.com/newsroom/images/passions/education/Apple_gallaudet-university_10072020.jpg.landing-gradient-large.jpg"/>
                            <SecondNewsCard title="Hoa mới của Laven" text="December 12, 2020" image="https://www.apple.com/newsroom/images/passions/education/Apple_gallaudet-university_10072020.jpg.landing-gradient-large.jpg"/>
                            <SecondNewsCard title="Hoa mới của Laven" text="December 12, 2020" image="https://www.apple.com/newsroom/images/passions/education/Apple_gallaudet-university_10072020.jpg.landing-gradient-large.jpg"/>
                        </div>
                    </div>
                </section>
                <section>
                    <SectionHeader text="Hoa Nổi Bật" title="CÁC SẢN PHẨM NỔI BẬT" action="Xem Tất Cả" action_link="/shop/all"/>
                    <div className="list">
                    {productsData.reverse().map((item, i) => {
                        if(i < 6) {
                            return <ProductCard title={item.title} price={item.price} image={`${item.banner}`} product_id={item._id} key={`product-0-${i}`}/>
                        }
                    })}
                    </div>
                </section>
            </div>
        )
    }
}

export default Home;